import kotlinx.css.maxWidth
import kotlinx.css.padding
import kotlinx.css.pct
import kotlinx.css.px
import material_ui.MaterialGrid
import material_ui.MaterialPaper
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.a
import react.dom.h1
import react.dom.h3
import react.dom.p
import styled.css
import styled.styledDiv
import styled.styledImg

class App: RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        MaterialGrid {
            attrs {
                container = true
                spacing = 1
            }
            MaterialGrid {
                attrs {
                    item = true
                    xs = 12
                }
                MaterialPaper {
                    styledDiv {
                        css {
                            padding(16.px)
                        }
                        h1 { +"/PS" }
                        a("https://www.acmicpc.net/user/movie_jo") {
                            p { +"Baekjoon Online Judge" }
                        }
                        a("http://codeforces.com/profile/movie_jo") {
                            p { +"Codeforces" }
                        }
                        a("http://icpckorea.org/2013-daejeon/regional") {
                            p { +"ACM-ICPC 2013 Daejeon Regional: 14th place (team: Codingsori)" }
                        }
                        a("http://icpckorea.org/2014-daejeon/regional") {
                            p { +"ACM-ICPC 2014 Daejeon Regional: 19th place (team: CPSLab)" }
                        }
                    }
                }
            }
            MaterialGrid {
                attrs {
                    item = true
                    xs = 12
                }
                MaterialPaper {
                    styledDiv {
                        css {
                            padding(16.px)
                        }
                        h1 { +" " }
                        a("https://play.google.com/store/apps/details?id=hangul.main") {
                            MaterialGrid {
                                attrs {
                                    xs = 12
                                    container = true
                                    alignItems = "center"
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 2
                                    }
                                    styledImg(src="https://lh3.googleusercontent.com/S3x1taWTtxeK2jAHr8EHmbsJiYgF0NYMbj_QI3qDsCQjzYwykyMrBC90NfoUXUxn36k=s360-rw") {
                                        css { maxWidth = 70.pct }
                                    }
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 10
                                    }
                                    h3 { +"Bahasa Korea Untuk Kita Semua" }
                                }
                            }
                        }
                        p { +"      " }
                        p { +"tts(text to speech)   " }

                        a("https://play.google.com/store/apps/details?id=com.movie.notinote") {
                            MaterialGrid {
                                attrs {
                                    xs = 12
                                    container = true
                                    alignItems = "center"
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 2
                                    }
                                    styledImg(src="https://lh3.googleusercontent.com/U3VhQjNWxeyjfQ98dPYMLycPSm7da9fRr7fqRb2-pDQ4Z4eGZBdQQmMshsmPZrNAUhAt=s360-rw") {
                                        css { maxWidth = 70.pct }
                                    }
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 10
                                    }
                                    h3 { +" -   " }
                                }
                            }
                        }
                        p { +"Notification Bar    " }
                        p { +"Kotlin " }
                        p { +"   (Navigation/Room Database/Preference/RecyclerView )" }

                        a("https://play.google.com/store/apps/details?id=com.movie.white") {
                            MaterialGrid {
                                attrs {
                                    xs = 12
                                    container = true
                                    alignItems = "center"
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 2
                                    }
                                    styledImg(src="https://lh3.googleusercontent.com/e4cAm15x7wP4LsQ_TudoRuBU6Gy1Rv-Mequs8I7ruDnv1DDpq5o8j23pptIwFPV4Fwk=s360-rw") {
                                        css { maxWidth = 70.pct }
                                    }
                                }
                                MaterialGrid {
                                    attrs {
                                        item = true
                                        xs = 10
                                    }
                                    h3 { +"White" }
                                }
                            }
                        }
                        p { +"         " }
                        p { +"Kotlin " }
                        p { +"UI   (Touch Event  ValueAnimation, ViewPropertyAnimation )" }
                        p { +"  " }
                    }
                }
            }
        }
    }
}
